import {useState, useEffect} from "react";
import Player from './components/player';
function App() {
  const [songs]= useState([
    {
      title: "Speak!",
      artist: "Adonis D. Ross",
      img_src:"./img/SongsFromTheHeartCover.jpg",
      src: "./songs/Speak.m4a",
    },
    {
      title: "A Change Is Gonna Come",
      artist: "Adonis D. Ross",
      img_src:"./img/AntiCover.jpg",
      src: "./songs/A+change+is+gonna+come(Sam+Cooke+Cover).m4a",
    },
    {
      title: "If You Want Me To Stay (Sly and the family stone cover",
      artist: "Adonis D. Ross",
      img_src:"./img/AntiCover.jpg",
      src: "./songs/If+You+Want+Me+To+Stay(Sly+Stone+Cover)(1).m4a",
    },
  ])
  const [currentSongIndex, setCurrentSongIndex] = useState(0);
  const [nextSongIndex, setNextSongIndex] = useState(currentSongIndex + 1);
  useEffect(()=>{
setNextSongIndex(()=>{
  if(currentSongIndex + 1 > songs.length - 1){
    return 0;
  } else {
    return currentSongIndex +1;
  }

});
  }, [currentSongIndex, songs.length]);
  return (
    <div className="App">
    <Player 
    currentSongIndex={currentSongIndex}
    setCurrentSongIndex={setCurrentSongIndex}
    nextSongIndex={nextSongIndex}
    songs={songs}
    />
    </div>
  );
}

export default App;
